@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-300.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-300.woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-regular.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-regular.woff"); }

@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-600.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-600.woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-700.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-700.woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  position: relative;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: black;
  background-color: white; }

a {
  text-decoration: none; }

img {
  border-style: none;
  max-width: 100%; }

.wrapper {
  margin: 0 auto; }

@media (max-width: 767px) {
  .wrapper {
    min-width: 320px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .wrapper {
    max-width: 768px; } }

@media (min-width: 1024px) {
  .wrapper {
    max-width: 1024px; } }

@media (max-width: 767px) {
  .page-header {
    min-height: calc(390px + 310px + 51px); } }

@media (min-width: 768px) and (max-width: 1023px) {
  .page-header {
    min-height: calc(340px + 292px + 51px); } }

@media (min-width: 1024px) {
  .page-header {
    min-height: calc(825px); } }

@media (max-width: 767px) {
  .facts {
    min-width: 837px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .facts {
    min-width: 496px; } }

@media (min-width: 1024px) {
  .facts {
    min-width: 503px; } }

@media (max-width: 767px) {
  .types {
    min-width: 1319px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .types {
    min-width: 949px; } }

@media (min-width: 1024px) {
  .types {
    min-width: 1038px; } }

@media (max-width: 767px) {
  .video {
    min-height: calc(162px + 271px); } }

@media (min-width: 768px) and (max-width: 1023px) {
  .video {
    min-height: calc(282px + 216px); } }

@media (min-width: 1024px) {
  .video {
    min-height: calc(522px + 282px); } }

@media (max-width: 767px) {
  .page-footer {
    min-width: 619px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .page-footer {
    min-width: 813px; } }

@media (min-width: 1024px) {
  .page-footer {
    min-width: 1002px; } }
